.mobile {
    background-color: white;
    height: 100vh;
    width: 60vw;
    position: absolute;
    right: 0px;
    top: -6px;
    padding: 18px 12px;
    display: flex;
    flex-direction: column;
}

.close-icon {
    display: flex;
    justify-content: flex-end;
    font-size: 24px;
}

.mobile-option {
    font-size: 24px;
    margin: 12px 0px;
    display: flex;
}

.mobile-option a{
    text-decoration: none;
    color: #028ca1;
    display: flex;
}